.root {
  background-color: #151515;
  margin: 20px 20%;
  padding: 50px;
  border: 2px solid #323232;
  border-radius: 10px;
  text-align: center;
}

.start {
  text-align: center;
  padding: 10px 40px;
  border-radius: 10px;
  outline: none;
  border: 1px solid black;
  cursor: pointer;
  margin: 50px;
}

.answer {
  margin: 10px;
  padding: 10px;
  outline: none;
  border-radius: 10px;
  border: 1px solid black;
  font-size: 16px;
  text-align: center;
}

@media only screen and (max-width: 600px) {
  .answer{
    width:60%;
  }
  .start{
    margin: 10px;
    padding: 10px;
    outline: none;
    border-radius: 4px;
    border: 1px solid black;
    font-size: 15px;
    text-align: center;
  
  }
  .start-p{
    width:10%;
    margin-left: 10px;
